<template> 
  <div style='position:relative'>
    <div class="legend" style='width:20%'>
      <div class="flex col-12 px0">
        <label class='col-5 px0'></label>
        <label class="red_bar inner_text col-7 px0">Unhandled Tasks</label>
      </div>
      <div class="flex col-12 px0">
        <label class='col-5 px0'></label>
        <label class="blue_bar inner_text col-7 px0">Tasks be responsible for </label>
      </div>
    </div>
    <full-calendar v-if="ready" :config="config" :events="events"/>
    <button type='button' id='calendar_card_opener' @click="trigger_open_card" style='display:none;'></button>
    <input type='hidden' id='calendar_record_id' value=''/>
    <input type='hidden' id='calendar_value' value=''/>
    <div class='center_card' v-if="open_card" style='z-index:10000;margin-left:25%;margin-right:25%;margin-top:15%'>
      <div class="card">
        <div class="card-header">
          <div class="flex">
            <strong>View Record {{ record_id }}</strong>
            <i class="fa fa-window-close" aria-hidden="true" style='margin-left:auto;margin-top:0.25rem;margin-right:0.5rem;cursor:pointer' @click="close_overlay"></i>
          </div>
        </div>    
        <div class="card-body">
          <div class="col-12 flex" style='margin-bottom:0.35rem'>
              <div class="col-3 px0">Requested By</div>
              <div class="col-1">:</div>
              <div class="col-8 px0">{{ Req_name }}</div>
          </div>
          <div class="col-12 flex" style='margin-bottom:0.35rem'>
              <div class="col-3 px0">Description</div>
              <div class="col-1">:</div>
              <div class="col-8 px0">{{ Descript }}</div>
          </div>
          <div class="col-12 flex" style='margin-bottom:0.35rem'>
              <div class="col-3 px0">Location</div>
              <div class="col-1">:</div>
              <div class="col-8 px0">{{ location }}</div>
          </div>
          <div class="col-12 flex" style='margin-bottom:0.35rem'>
              <div class="col-3 px0">Due Date</div>
              <div class="col-1">:</div>
              <div class="col-8 px0">{{ dd }}</div>
          </div>
          <div class="col-12 flex" style='margin-bottom:0.35rem'>
              <div class="col-3 px0">Details</div>
              <div class="col-1">:</div>
              <div class="col-8 px0"><u class='redirect'><a @click="goto_ticket">View Ticket(s)</a></u></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import moment from 'moment';
	export default {
  data () {
    return {
      location:'',
      dd:'',
      Descript:'',
      Req_name:'',
      record_id:'',
      open_card:false,
      ready:false,
      test_content:'',
			events: [],
			config: {
        defaultView: 'month',
				eventRender: function(event, element) {
          if(event.end!=null){
            var formData = new FormData();
            formData.append('record_id',event.title);
            formData.append('new_due_date',event.end._i);
            axios.post('RequestPortal/backend/change_calendar.php',formData)
              .then(
                (response)=>{
                  
                }
              )
          }
        },
        eventClick: function(event) {   
          document.getElementById('calendar_record_id').value = event.title;
          document.getElementById('calendar_card_opener').click();              
        }
      },
    }
  },
  async created(){
      await axios.get('RequestPortal/backend/get_calendar.php')
        .then(
          (response)=>{
              if(response.data.length>0){
                var i = 0;
                for(i=0;i<response.data.length;i++){
                  this.events[i] = [];
                  this.events[i]['extendedProps'] = [];
                  this.events[i]['title'] = '#'+response.data[i].title;
                  this.events[i]['start'] = response.data[i].start;
                  this.events[i]['end'] = response.data[i].end;
                  this.events[i]['extendedProps']['Location'] = response.data[i].extendedProps.Location;
                  this.events[i]['extendedProps']['Req_Name'] = response.data[i].extendedProps.Req_Name;
                  //this.events[i]['description'] = response.data[i].description;
                  this.events[i]['backgroundColor'] = response.data[i].backgroundColor;
                  this.events[i]['borderColor'] = response.data[i].borderColor;
                }
              }
              this.open_card = false;
              this.ready = true;
          }
        )
  },
  mounted(){
  },
  updated(){
    document.getElementsByClassName('fc-right').innerHTML = "HI";
  },
  methods:{
      async trigger_open_card(){
        this.record_id = document.getElementById('calendar_record_id').value;
        this.fetch_request();
        this.open_card = true;
      },
      async fetch_request(){
        var formData = new FormData();
        formData.append('record_id',this.record_id);
        await axios.post('RequestPortal/backend/fetch_request.php',formData)
          .then(
              (response)=>{
                  this.Req_name = response.data[0].Req_Name;
                  this.Descript = response.data[0].Description;
                  this.location = response.data[0].Location;
                  this.dd = response.data[0].Due_date;
              }
          )
      },
      goto_ticket(){
        var tmp_id = this.record_id;
        tmp_id = tmp_id.replace('#','');
        document.getElementById('tmp_id').value = tmp_id;
        document.getElementById('tmp_id_2').value = 'handler';
        this.$router.push('ticket');
      },
      close_overlay(){
        this.open_card = false;
      }
  }
}

</script>
<style>
.fc-day-header{
  height:3rem!important;
  font-size:1.5rem!important;
}
.fc-day-number{
  font-size:1rem!important;
}
div.fc-content{
  font-size:1rem!important;
  height:1.2rem!important;
}
span.fc-title,span.fc-time{
  color:rgba(255,255,255,0.8)!important;
}
.fc-event .fc-bg {
  opacity:1!important;
}
td.fc-axis.fc-widget-content > span{
  font-size:0.8rem!important;
}
span.fc-day-number{
  color:#000000!important;
  font-weight: 400;
}
.fc-bg{
  background-color:rgb(250,239,210)!important;
  opacity:1!important;
}
.center_card{
  position:absolute;
  left:0;
  right:0;
  top:0;
  bottom:0;
  margin: auto;
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
.flex{
  display:flex;
  flex-direction: row;
}
.px0{
  padding-left:0;
  padding-right:0;
}
tr > td.fc-today{
  background-color:rgb(172, 242, 194)!important;
}
.fc-right{
  width:19.5%!important;
  height:1rem!important;
}
div.fc-right > div.fc-button-group{
  display:none!important;
}
</style>
<style scoped>
.redirect{
  cursor:pointer;
  color:rgba(0,0,0,0.85);
}
.redirect:hover{
  color:rgba(0,0,0,0.7);
}
.legend{
  position:absolute;
  right:0;
  top:0;
  font-size:1.2rem;
  padding-top:0.2rem;
}
.flex{
  display:flex;
  flex-direction:row;
}
.red_bar{
  background-color:rgb(238,28,37);
  color:rgba(255,255,255,0.8);
  font-weight: bold;
}
.blue_bar{
  background-color:rgb(114, 163, 232);
  color:rgba(255,255,255,0.8);
  font-weight: bold;
}
.inner_text{
  font-size:0.75rem!important;
  text-align:center;
}
.px0{
  padding-left:0;
  padding-right:0;
}
</style>


